:root {
    --white: #ffffff;
    --white-blue: #e5e9ef;
    --white-blue-darker: #e4e7eb;
    --gray: #ccc;
    --dark-gray: #343a40;
    --black: #2c2c2c;
    /*--light-blue: #42adde;*/
    --blue: #37b5ff;
    --light-blue:#d5eaff;
    --dark-blue: #0066d5;
    --red: #ff5252;
}

.black-color {
    color: #2c2c2c;
    color: var(--black);
}

.blue-color {
    color: #37b5ff;
    color: var(--blue);
}
button {
    outline: none;
    border: 1px solid var(--gray);
    background-color: white;
    border-radius: 20px;
    min-height: 38px;
    transition: background-color 0.3s ease-in-out;
}
button:not(:disabled):hover {
    border-color: #aaa;
}
button:disabled {
    opacity: 0.6;
}

.primary-button {
    background-color: var(--blue);
    color: white;
    box-shadow: 0 5px 8px 0 rgb(41 152 255 / 21%);
    /*border: 1px solid var(--dark-blue);*/
    border: 0;
}

.swal2-confirm:not(:disabled):hover,
.primary-button:not(:disabled):hover {
    background-color: var(--dark-gray);
}

.secondary-button {
    background-color: white !important;
    color: var(--blue) !important;
}
.secondary-button:not(:disabled):hover{
    border: 1px solid var(--blue) !important;
}

button.secondary-button.black {
    color: var(--black) !important;
    border:1px solid var(--gray);
}

button.secondary-button.black:hover{
    border: 1px solid var(--black) !important;
}

.dangerous-button {
    background-color: white;
    color: var(--red);
}
.dangerous-button:not(:disabled):hover {
    border: 1px solid var(--red);
}

.link-button {
    background-color: transparent;
    border:0;
    color: black;
    display: flex;

    flex-direction: row;
    grid-gap: 5px;
    gap: 5px;
    align-items: center;
    transition: all 0.3s ease-in-out;
}

.link-button:not(:disabled):hover {
    opacity: 0.6;
}

.sidebar-statistics,
.sidebar-button {
    text-align: right;
    border: 0;
    background: transparent;
    display: flex;
    flex-direction: row;
    grid-gap: 5px;
