.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

body {
    background-color: #eaeff5;
}

.landing-page-layout{
    padding: 30px;
    -webkit-padding-before: 0;
            padding-block-start: 0;
    background-color: #eaeff5;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    height: 100vh;
    width: 100vw;
    background-image: url('/images/landing-page/background-2.png');
    text-align: center;

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    color: #282b3c;
}

.header {
    padding: 20px;
    height: 100px;
    text-align:right;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-inline: 0;
}
.header a,
.footer a {
    cursor: pointer;
}
.header a:hover {
    opacity: 0.6;
}
.header a img,
.footer a img {
    height: 20px;
}

.header .choose-language {
    display: flex;
    grid-gap: 8px;
    gap: 8px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    align-items: center;
}
.header .choose-language a {
    cursor: default;
    display: flex;
}
.header .choose-language a img {
    display: flex;
    align-self: center;
    margin-inline: 5px;
}
.header .choose-language a:hover {
    opacity: 1;
}

.header .choose-language select {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    background-color: transparent;
    border: 0;
    cursor: pointer;
}
.header .choose-language select:hover {
    opacity: 0.6;
}
.main-part {
    width: 40%;
    margin: 0 auto;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
}
.main-part .plan-your-trip-header {
    font-size: 30px;
    text-transform: uppercase;
    color: #282b3c;
}
.main-part .plan-your-trip-header .trip {
    font-size: 50px;
    color: var(--blue);
}
.main-part .logo-container{
    -webkit-user-select: none;
    user-select: none;
    width: 100%;
}
.main-part .slogan {
    display: flex;
    flex-direction: column;
    padding-block: 40px;
}
.main-part .start-now-button button:not(:disabled):hover {
    background-color: var(--dark-gray);
}
.footer {
    padding: 20px;
    height: 100px;
    display: flex;
    width: 100%;
    flex-direction: row;
    grid-gap: 10px;
    gap: 10px;
    justify-content: center;
}
.footer a {
    color: #a6a6a6 !important;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-items: center;
    cursor: default;
}
.footer a img {
    height: 25px;
    margin-inline: 5px;
}
